import React, { useEffect, useState } from "react";
import Table from "../../components/table";
import useUser from "../../store/users";
import Button from "../../components/button";
import AddUser from "./AddUser";
import { toast } from "react-toastify";
import { IconButton, Stack ,Tab, Tabs} from "@mui/material";
import { Delete } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import Category from "../../components/UI/Category";
import UploadCsv from "../store/UploadCsv";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import Tooltip from '@mui/material/Tooltip';

const UserMainPage = () => {
  const [tab , setTab] = useState("Users")
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [csvData, setCsvData] = useState([])
  const [page, setPage] = useState(0);
  const [file, setFile] = useState(null);
  const [data, setData] = useState({
  });
  const { list, create,update, remove,opratorList,createOperator,removeOperator,updateOperator } = useUser();

  useEffect(() => {
    fetchUsers();
  }, [page, tab]);

  const fetchUsers = async () => {
    setLoading("fetch");
    const [_list, _count] = tab === "Users" ? await list(page + 1) : await opratorList(page + 1)
    setUsers(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    setLoading("fetch")
    tab === "Users" ? await create(payload): await createOperator(payload)
    setData({});
    setModalOpen(false);
    await fetchUsers();
    setLoading(false);
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    tab === "Users" ? await remove(id): await removeOperator(id);
    await fetchUsers();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin,type) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    const payload2 ={
      "status":is_admin===true?false:true
    }
    tab === "Users" ? await update(id,type==="status"?payload2:payload):updateOperator(id,type==="status"?payload2:payload)
    await fetchUsers();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 1,
      title: "Email",
      render: (rowData) => rowData.email,
    },
    {
      id: 2,
      title: "User Name",
      render: (rowData) => rowData.username,
    },
    {
      id: 3,
      title: "Full Name",
      render: (rowData) => rowData.fullname,
    },
    {
      id: 4,
      title: "Employee Id",
      render: (rowData) => rowData.employee_id,
    },
    {
      id: 5,
      title: "Role",
      render: (rowData) => <Category title={rowData.is_admin?"admin":"Na"}/>,
    },
    {
      id: 6,
      title: "Actions",
      render: (rowData) => (<>
        <Stack flexDirection={'row'} sx={{gap:1}}>
        <Button width={"10%"} color={rowData.status?"green":"red"}
        onClick={() => handleUpdate(rowData?.id,rowData?.status,"status")}
        >
          {rowData.status?"Active":"Inactive"}
        </Button>
      <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData?.id)}
        >
          <Delete color="error" fontSize="small" />
        </IconButton>
        {tab==="Users" && rowData?.is_admin ===false ?(
        <Tooltip title="Make Admin">
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleUpdate(rowData?.id,rowData?.is_admin,"delete")}
        >
        <AdminPanelSettingsIcon color={rowData.is_admin?"error":"success"} fontSize="small" />
        </IconButton>
        </Tooltip>
        ):null}
      </Stack>
        </>
      ),
    },
  ];

  const handleSubmitCSV = async()=>{
     csvData.map(async(csvitem)=> {
      if(tab === "Users"){
        if(csvitem.email!==""){
          await create(csvitem)
        }else{
          return
        }
      }else{
        if(csvitem.employee_id!==""){
          await createOperator(csvitem)
        }else{
          return
        }
      }
     })
     setModalOpen(false);
     await fetchUsers(page =1)
   }
  return (
    <div className="mx-3 my-2">
      <Tabs value={tab} onChange={(_e, v) => {setTab(v);setPage(0)}} className="mb-2">
          {["Users", "Operators"].map((label, i) => (
            <Tab label={label} value={label} />
          ))}
        </Tabs>
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          {tab}<span className="fs-30 ml-1 c-gray">({users.length})</span>
        </h1>
        <Stack justifyContent="flex-end" direction="row">
          <Button onClick={() => setModalOpen("csvUser")} bordered classes="mr-1">
            Upload CSV
          </Button>
        <Button onClick={() => setModalOpen(true)}>Create {tab}</Button>
        </Stack>
      </div>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-500"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddUser
        open={modalOpen}
        onClose={() => setModalOpen(false)}
        data={data}
        setData={setData}
        onSubmit={handleAdd}
        loading={loading === "add"}
        tab={tab}
      />
      <UploadCsv
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csvUser"}
        onSubmit={handleSubmitCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
        tab={tab}
      />
    </div>
  );
};

export default UserMainPage;
