import React,{useState} from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";

const AddCompany = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const USER_SCHEMA = [
    {
      title: "Model",
      placeholder: "Enter model",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Material Code",
      placeholder: "Enter code",
      type: SCHEMA_TYPES.INPUT,
      name: "Line",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Barcode Identifier",
      placeholder: "Enter identifier",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Customer",
      placeholder: "Select customer",
      type: SCHEMA_TYPES.SELECT,
      name: "serialization_method",
      validator: VALIDATORS.REQUIRED,
      keyValues: [],
    },
    {
      title: "Description",
      placeholder: "Enter description",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} MODEL</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
        <Stack sx={{mt:1, flexDirection:'row',justifyContent:"space-between",paddingRight:2}}>
        <FormControl>
          <FormLabel id="demo-radio-buttons-group-label">Fixed Cycle Time</FormLabel>
          <RadioGroup
            aria-labelledby="demo-radio-buttons-group-label"
            defaultValue="Yes"
            name="radio-buttons-group"
          >
            <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
            <FormControlLabel value="No" control={<Radio />} label="No" />
          </RadioGroup>
      </FormControl>
        </Stack>
     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddCompany;
