import React, { useEffect, useState } from "react";
import { Route, Routes as AllRoutes } from "react-router-dom";
import TenantsMainPage from "./pages/tenants";
import UserMainPage from "./pages/users";
import {ROLE} from "./helpers/constants"
import { useDispatch, useSelector } from "react-redux";
import { REDUCER_TYPES } from "./store";
import { resetAuth } from "./store/api/utils";
import MasterList from "./pages/store/StoreMaster";
import StoreInbound from "./pages/store/StoreInbound";
import StoreOutbound from "./pages/store/StoreOutbound";
import OutboundHistory from "./pages/store/OutboundHistory";
import StoreOrder from "./pages/store/Order"
import Error from "./pages/error";
import MachinePage from './pages/machine/index'
import CompanyPage from './pages/company/index'
import PlantPage from './pages/plant/index';
import DivisionPage from "./pages/division"; 
import LinePage from "./pages/line";
import AssetPage from "./pages/asset";
import GroupsPage from './pages/groups'
import UserTenantPage from "./pages/usersTenant";
import Dashboard from "./pages/dashboard"
import TimelineBar from "./pages/timeline-bar";
import DowntimePage from "./pages/downtime"
import ModalMasterPage from "./pages/model_master";

const Routes = () => {

  const dispatch = useDispatch();
  const [hydrated, setHydrated] = useState(false);
  const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
 const role = state?.user?.data?.role
  const routes = [ 
    {
      path:'/timelinebar',
      element:<TimelineBar/>
    },
    {
      path:'/Dashboard',
      element:<Dashboard/>
    },
    {
      path:'/error',
      element:<Error/>
    },
    {
      path: "/users",
      element: role === ROLE.ADMINISTRATOR?<UserMainPage />:<Error/> ,
    },
    {
      path: "/tenants",
      element: role === ROLE.SUPERADMIN?<TenantsMainPage />:<Error/>,
    },
    {
      path: "/store-master",
      element: role === ROLE.ADMINISTRATOR?<MasterList />:<Error/>,
    },
    {
      path: "/downtime-master",
      element: role === ROLE.ADMINISTRATOR?<DowntimePage />:<Error/>,
    },
    {
      path: "/model-master",
      element: role === ROLE.ADMINISTRATOR?<ModalMasterPage />:<Error/>,
    },
    {
      path: "/store-inbound",
      element: <StoreInbound />,
    },
    {
      path: "/store-outbound",
      element: <StoreOutbound />,
    },
    {
      path: "/store-outbound-history",
      element: <OutboundHistory />,
    },
    {
      path: "/settings",
      element: <h1>Settings</h1>,
    },
    {
      path: "/logout",
      element: <h1>Log</h1>,
    },
    {
      path:'/order',
      element:<StoreOrder/>
    },
    {
      path:'/machinepage',
      element:<MachinePage/>
    },
    {
      path:'/companypage',
      element:role === ROLE.ADMINISTRATOR?<CompanyPage/>:<Error/>
    },
    {
      path:'/plantpage',
      element:role === ROLE.ADMINISTRATOR?<PlantPage/>:<Error/>
    },
    {
      path:'/divisionpage',
      element:role === ROLE.ADMINISTRATOR?<DivisionPage/>:<Error/>
    },
    {
      path:'/line',
      element:role === ROLE.ADMINISTRATOR?<LinePage/>:<Error/>
    },
    {
      path:'/asset',
      element:role === ROLE.ADMINISTRATOR?<AssetPage/>:<Error/>
    },
    {
      path:'/group',
      element:role === ROLE.ADMINISTRATOR?<GroupsPage/>:<Error/>
    },
    {
      path:'/tenant-Users',
      element:role === ROLE.SUPERADMIN?<UserTenantPage/>:<Error/>
    },

  ];
  useEffect(() => {
    resetAuth(dispatch, state);
    setHydrated(true);
  }, []);

  if (!hydrated) {
    return null;
  }
  return (
    <AllRoutes>
      {routes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
    </AllRoutes>
   
  );
};

export default Routes;
