import React,{useState} from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";

const AddCompany = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {
  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Lines",
      placeholder: "Enter Line",
      type: SCHEMA_TYPES.INPUT,
      name: "Line",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Threshold (seconds)",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    }
   
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(500px,90vw)"
    >
      <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} DOWNTIME REASON</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
        <Stack sx={{mt:1, flexDirection:'row',justifyContent:"space-between",paddingRight:2}}>
        <FormControl>
          <FormLabel id="demo-radio-buttons-group-label">Status</FormLabel>
          <RadioGroup
            aria-labelledby="demo-radio-buttons-group-label"
            defaultValue="enable"
            name="radio-buttons-group"
          >
            <FormControlLabel value="enable" control={<Radio />} label="Enable" />
            <FormControlLabel value="disable" control={<Radio />} label="Disable" />
          </RadioGroup>
      </FormControl>

      <FormControl>
          <FormLabel id="demo-radio-buttons-group-label">Include in OEE Calculation</FormLabel>
          <RadioGroup
            aria-labelledby="demo-radio-buttons-group-label"
            defaultValue="As Productive Time"
            name="radio-buttons-group"
          >
            <FormControlLabel value="As Downtime" control={<Radio />} label="As Downtime" />
            <FormControlLabel value="As Productive Time" control={<Radio />} label="As Productive Time" />
          </RadioGroup>
      </FormControl>

        </Stack>
     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          Cancel
        </Button>
        <Button onClick={onSubmit} loading={loading}>
          Create
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddCompany;
