import {
  Box,
  Drawer as MuiDrawer,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Icon,
  AppBar,
  Toolbar,
  Avatar,
  Stack,
  Menu,
  MenuItem,
  Button
} from "@mui/material";
import React, { useEffect, useState } from "react";
import Divider from '@mui/material/Divider';
import { useSelector } from "react-redux";
import {
  ChevronLeft,
  ChevronRight,
  Construction,
  Home,
  Logout,
  PeopleOutline,
  Settings,
  Widgets,
  ShoppingCartOutlined,
  email,
} from "@mui/icons-material";
import PersonIcon from '@mui/icons-material/Person';
import EmailIcon from '@mui/icons-material/Email';
import InventoryIcon from '@mui/icons-material/Inventory';
import PrecisionManufacturingIcon from '@mui/icons-material/PrecisionManufacturing';
import TimelineIcon from '@mui/icons-material/Timeline';
import HourglassBottomIcon from '@mui/icons-material/HourglassBottom';
import LogoutIcon from '@mui/icons-material/Logout';
import styled from "@emotion/styled";
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import Routes from "../../routes";
import useAuth from '../../store/auth'
import { REDUCER_TYPES } from "../../store";
import {storeUser} from "../../store/api/utils";
import {ROLE} from '../../helpers/constants';
import ModelTrainingIcon from '@mui/icons-material/ModelTraining';


const drawerWidth = 180;

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    width: drawerWidth,
    overflowX: "hidden",
    "& .MuiDrawer-paper": {
      width: drawerWidth,
      overflowX: "hidden",
    },
  }),
  ...(!open && {
    overflowX: "hidden",
    width: "50px",
    "& .MuiDrawer-paper": {
      overflowX: "hidden",
      width: "50px",
    },
  }),
}));

const usr = {
  title: "Users",
  icon: PeopleOutline,
  path: "/users",
}

const ten =  {
  title: "Tenants",
  icon: InventoryIcon,
  path: "/tenants",
}
const machine = {
  title: "Machine Master",
  icon: PrecisionManufacturingIcon,
  path: "/companypage",
}
const downtime = {
  title: "Downtime Master",
  icon: HourglassBottomIcon,
  path: "/downtime-master",
}
const storeMaster= {
  title: "Store Master",
  icon: Construction,
  path: "/store-master",
}

const modelMaster ={
  title:"Model Master",
  icon:ModelTrainingIcon,
  path:"/model-master"
}

const menu = [
  {
  title: "Groups",
  icon: Construction,
  path: "/group",
},
{
  title: "Inbound List",
  icon: Widgets,
  path: "/store-inbound",
},
{
  title: "Outbound",
  icon: Widgets,
  path: "/store-outbound",
},
{
  title: "Outbound History",
  icon: TimelineIcon,
  path: "/store-outbound-history",
},
{
  title: "Settings",
  icon: Settings,
  path: "/settings",
},
{
  title: "Store Order",
  icon: ShoppingCartOutlined,
  path: "/order",
},

]
const NavigationLayout = () => {
  const [menu_items,setMenu_items] = useState([])
  const isLoggedIn = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const user = useSelector((state) => state[REDUCER_TYPES.AUTH].user?.data)
  const {logout,profile2} = useAuth()
  const [open, setOpen] = useState(true);
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const toggle = () => setOpen((val) => !val);
  const [anchorEl, setAnchorEl] = useState(null);
  const openpop = Boolean(anchorEl);

  useEffect(()=>{ 
    const sideMenu = []
    console.log("redux user",user?.role)
    if(user?.role === ROLE.SUPERADMIN ||localStorage.getItem('role') === ROLE.SUPERADMIN){
      sideMenu.push(ten,...menu)
      setMenu_items(sideMenu)
    }else if(user?.role === ROLE.ADMINISTRATOR ||localStorage.getItem('role') === ROLE.ADMINISTRATOR){
      sideMenu.push(usr,storeMaster,machine,downtime,modelMaster,...menu)
      setMenu_items(sideMenu)
    }else{
      setMenu_items(menu)
    }
    if(!localStorage.getItem('t_token')){
      console.log("islogin",user)
    // toast.warn("Please Login!")
      navigate('/login',{replace:true})
    }
  },[])

  const handleClick = (event) => {
    console.log("popup",event.currentTarget)
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
   
    <>
    {/* {isLoggedIn &&( */}
     <div>
      <Drawer open={open} variant="permanent" onClose={toggle} elevation={0}>
        <Box className="flex items-center justify-around mt-2">
          <h3 className={`${!open ? "display-none" : "ml-2"}`}>TRACEABILITY</h3>
          <IconButton onClick={toggle}>
            {open ? <ChevronRight /> : <ChevronLeft />}
          </IconButton>
        </Box>
        <Box sx={{ width: drawerWidth }} role="presentation">
         <List dense>
            {menu_items.map(({ title, path, icon }) => (
              <ListItem
                key={path}
                className={`py-1 ${pathname === path && "bg-opaque"}`}
                button
                onClick={() => navigate(path)}
              >
                <ListItemIcon className="minw-40">
                  <Icon
                    component={icon}
                    fontSize="small"
                    className={`py-2 ${
                      pathname === path ? "c-primary" : "c-gray"
                    }`}
                  />
                </ListItemIcon>
                <ListItemText
                  primary={title}
                  className={`fw-500 ${pathname === path && "c-primary"}`}
                />
              </ListItem>
            ))}
            {/* <ListItem>
              <ListItemIcon>
                <Home/>
                <ListItemText primary='submenu'/>
              </ListItemIcon>
            </ListItem> */}
          </List>     
        </Box>
      </Drawer>
      <Box
        sx={{
          position: "relative",
          left: open ? drawerWidth : "50px",
          overflow: "hidden",
        }}
      >
        <AppBar position="static" color="default" variant="outlined">
          <Toolbar
            sx={{ maxWidth: `calc(100vw - ${open ? 180 : 50}px)` }}
            disableGutters
            className="h-50 minh-50 w-full"
          >
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="space-between"
              className="w-full px-3"
            >
              <Box></Box>
              <div>
              <Button
                id="basic-button"
                aria-controls={openpop ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={openpop ? 'true' : undefined}
                onClick={handleClick}
              >
              <Avatar
                src="https://www.svgrepo.com/show/382099/female-avatar-girl-face-woman-user-2.svg"
                sx={{ height: "38px", width: "38px" }}
              /> </Button>
              <Menu
              
                id="basic-menu"
                anchorEl={anchorEl}
                open={openpop}
                onClose={handleClose}
                MenuListProps={{
                  'aria-labelledby': 'basic-button',
                }}
              >
                {/* <MenuItem onClick={handleClose}>Profile</MenuItem>
                <MenuItem onClick={handleClose}>My account</MenuItem> */}
                <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                <ListItemIcon>
                  <PersonIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>{user?.name}</ListItemText>
                </MenuItem>
                <Divider />
                <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                <ListItemIcon>
                  <EmailIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>{user?.email}</ListItemText>
                </MenuItem>
                <Divider />
                <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                <ListItemIcon>
                  <PersonIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>{user?.role}</ListItemText>
                </MenuItem>
                <Divider />
                <MenuItem sx={{py:0}} onClick={()=>{handleClose();logout()}}>
                <ListItemIcon>
                  <Logout fontSize="small" />
                </ListItemIcon>
                <ListItemText>Logout</ListItemText>
                </MenuItem>
                {/* <Divider /> */}
              </Menu>
             
              </div>
            </Stack>
          </Toolbar>
        </AppBar>
        <Box sx={{ maxWidth: `calc(100vw - ${open ? 180 : 50}px)` }}>
          <Routes />
        </Box>
      </Box>
    </div>
    {/* )} */}
    </>
          
  )
};

export default NavigationLayout;
