import { Divider, Grid } from "@mui/material";
import React, { useState,useLayoutEffect, useEffect } from "react";
import Button from "../button";
import { Add } from "@mui/icons-material";
import Input from "../input";

const JsonCreator = ({ setJson = Function.prototype, title = "Item" ,setItemss}) => {
  const [items, setItems] = useState([{ key: "", value: "" }]);
  useEffect(()=>{
    if(setItemss!==null){
    let temparr = []
     Object.entries(setItemss).reduce((acc, [key, value]) => {
      let obj ={ key: key, value: value}
      temparr.push(obj)
    }, {});
    setItems(temparr)
  }
  },[setItemss])

  const handeChange = (e, index) => {
    const { name, value } = e.target;
    items[index][name] = value;
    setItems([...items]);
    let newJson = {};
    items.forEach(({ key, value }) => {
      newJson[key] = value;
    })
    setJson({...newJson});
  };

  const addMore = () => {
    setItems((prev) => [...prev, { key: "", value: "" }]);
  };

  return (
    <>
      {items.map(({ key, value }, i) => (
        <>
          <Divider className="w-50p mx-auto h-10 flex items-center mt-4">
            <p className="fs-12 c-gray">
              {title} {i + 1}
            </p>
          </Divider>
          <Grid container>
            <Grid item xs={6}>
              <Input
                title="Label"
                name="key"
                width="90%"
                value={key}
                onChange={(e) => handeChange(e, i)}
              />
            </Grid>
            <Grid item xs={6}>
              <Input
                title="Value"
                name="value"
                width="90%"
                value={value}
                onChange={(e) => handeChange(e, i)}
              />
            </Grid>
          </Grid>
        </>
      ))}
      <Button classes="mx-auto my-4" onClick={addMore}>
        <Add className="mr-1" fontSize="small" /> Add {title}
      </Button>
    </>
  );
};

export default JsonCreator;
